import type {Repository} from '@github-ui/current-repository'
import {commitPath, comparePath} from '@github-ui/paths'

import type {Push} from '../types/activity-types'

export function compareButtonHref(push: Push, repo: Repository) {
  if (push.before && push.after) {
    return comparePath({repo, base: push.before, head: push.after}) // push
  } else if (push.before && !push.after) {
    return commitPath({owner: repo.ownerLogin, repo: repo.name, commitish: push.before}) // deleted
  } else if (!push.before && push.after) {
    return commitPath({owner: repo.ownerLogin, repo: repo.name, commitish: push.after}) // created
  }
}
